.PeriodicTableElement {
	position: relative;
	text-align: center;
	padding: 0.5em;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
	border: 1px solid hsl(200, 20%, 30%);
	margin: 0 -1px -1px 0;
	cursor: pointer;
	z-index: 1;
}

.PeriodicTableElement:hover {
	-webkit-box-shadow: inset 0 0 0 2px #fff;
	        box-shadow: inset 0 0 0 2px #fff;
	z-index: 2;
}

.PeriodicTableElement-number {
	font-size: 0.75em;
	position: absolute;
	top: 0.5em;
	left: 0.5em;
}

.PeriodicTableElement-symbol {
	display: block;
	font-size: 2em;
}

.PeriodicTableElement-long {
	display: block;
	font-size: 0.8em;
}

.PeriodicTableElement.PeriodicTableElement--selected,
.PeriodicTableElement.PeriodicTableElement--selected.color--highlighted {
	background-color: #fff;
	color: #222;
}



