.LinkButton {
	-webkit-appearance: none;
	-moz-appearance: none;
	cursor: pointer;
	color: #ddd;
	border: none;
	background: transparent;
	font: inherit;
	-ms-flex-align: normal;
	    align-items: normal;
	block-size: auto;
	-webkit-box-sizing: content-box;
	        box-sizing: content-box;
	display: inline;
	inline-size: auto;
	padding: 0;
	text-align: start;
}

.LinkButton:hover {
	text-decoration: underline;
	color: #fff;
}



